#display{
    font-size: 11px;
    font-family: 'Times New Roman', Times, serif;
    padding: 4.09%;
    position:relative;
    overflow-y: hidden;
    word-wrap: break-word;
white-space: pre-wrap;
word-break: break-word;
    
}
#nameDisplay{
    font-size: 20px;
    margin-bottom:1%;

}
#personalDisplay{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 3%;
    margin-bottom: 2.5%;
    border-bottom: 1px solid black;
}
.subheader{
    border-bottom: 1px solid black;
    font-weight: bold;
    font-size: 16px;
    margin:2% 0%;
}
.date{
    position: absolute;
    right:5%;

}
section{
    margin-bottom: 2%;
}