input{
    border-radius: 4px;
    border: 1px solid black;
}
.forms{
    width: 95%;
    height: 95%;
    border-radius: 20px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}
#personalForm{
   display: grid;
grid-template-rows: 2fr 2fr 2fr 4fr;
    width: 80%;
    height:80%;
}
#personalForm > textarea{
    resize: none;
    width:100%;
  
}
input{
    margin-bottom:10%;
}



#educationForm{
    display: grid;
    grid-template-rows: 2fr 2fr 2fr 1fr 2fr 1fr 2fr;
    
    width: 60%;
    height: 80%;
}


#workExperienceForm{
    display: grid;
    grid-template-rows: 2fr 2fr 1fr 2fr 1fr 2fr 2fr 1fr 2fr 1fr;
    width: 60%;
    height: 80%;
}
#workExperienceForm > input{
    margin-bottom: 5%;
}
#workExperienceForm > #responsibilityInput{
    margin-top: 2.5%;
    margin-bottom: 2.5%;
}

#submitResponsibilityButton{
    width:60%;
    justify-self:center;
    border-radius: 10px;
    border: 1px solid black;
    background-color: aquamarine;
    cursor: pointer;  

}
#submitResponsibilityButton:hover{
    background-color: hsl(160, 100%, 60%);  
  }
  #responsibilities{
   overflow-x: hidden;
   overflow-y: scroll;

  }

  #honorForm{
    display: grid;
    grid-template-rows: 2fr 1fr 2fr 4fr 1fr;
    width: 60%;

  }

  #honorForm > textarea{
    resize: none;
    height:8rem;
  }


