::-webkit-scrollbar {
  width: 4px;
  background-color: grey;
}

::-webkit-scrollbar-button:single-button {
  display: none;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(rgb(229, 122, 229), rgb(253, 253, 128));
}

body{
  width: 100vw;
  height:100vh;
  margin:0;
}

#app{
  width: 100vw;
  height:100vh;
  display: grid;
  grid-template-columns: 45% 45%;
  grid-template-rows: 88% 12%;
  column-gap: 10%;
  justify-items: center;
  align-items: center;
}
#formContainer{
  grid-column: 1/2;
  grid-row: 1/3;
  width: 70%;
  height:75%;
  background-color: rgb(255, 255, 255);
  display: flex;
  justify-content: center;
  align-items: center;

}
#display{
  grid-row: 1/3;
  grid-column: 2/3;
height:90%;
width: 69.5%;
background-color: rgb(255, 255, 255);
box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

}
#buttons{
  grid-column: 1/2;
  grid-row: 2/3;
  height:100%;
  width: 60%;
  display: grid;
grid-template-columns: 50% 50%;
grid-template-rows: 50% auto;
justify-items: center;

}
#formCount{
  grid-row: 2/3;
  grid-column: 1/3;

}

#buttons > button{  
  grid-row: 1/2;
  width: 70%;
  font-size: 16px;
  background-color: aquamarine;
  border: 1px black solid;
  border-radius: 20px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  cursor: pointer;  
}
#buttons>button:hover{
  background-color: hsl(160, 100%, 60%);  
}
button:hover{
  background-color: hsl(160, 100%, 60%);  
}
#previousFormButton{
  grid-column: 1/2;
  
}
#nextFormButton{
  grid-column: 2/3;
  
}

.addFormButton{
  width: fit-content;
  justify-self: center;
  font-size: 14px;
  background-color: aquamarine;
  border: 1px black solid;
  border-radius: 20px;
  cursor: pointer;  

}